<template>
	<div class="site-wrapper"
		:class="{'site-sidebar--fold':sidebarFold}"
		v-loading.fullscreen.lock="loading"
		element-loading-text="拼命加载中">
		<template v-if="!loading&&!isMicro">
			<main-navbar />
			<main-sidebar class="selfSty" />
			<div class="site-content__wrapper" :style="{'height':documentClientHeight + 'px' }">
				<main-content v-if="!$store.state.common.contentIsNeedRefresh" class="mainselfSty"/>
			</div>
		</template>
    <template v-else-if="!loading&&isMicro">

      <main-sidebar class="qiankunselfSty"/>
      <div class="site-content__wrapper qiankunwrapper" :style="{'height':documentClientHeight + 'px' }">
        <main-content v-if="!$store.state.common.contentIsNeedRefresh" class="qiankunmainselfSty"/>
      </div>
    </template>
	</div>
</template>

<script>
	import MainNavbar from './main-navbar'
	import MainSidebar from './main-sidebar'
	import MainContent from './main-content'
	export default{
		provide(){
			return{
				// 刷新
				refresh(){
					this.$store.commit('common/updateContentIsNeedRefresh',true)
					this.$nextTick(() => {
						this.$store.commit('common/updateContentIsNeedRefresh',false)
					})
				},
			}
		},
		data(){
			return{
				loading:true,
        isMicro: window.__POWERED_BY_QIANKUN__
			}
		},
		components:{
			MainNavbar,MainSidebar,MainContent
		},
		computed:{
			documentClientHeight:{
				get(){
				  return this.$store.state.common.documentClientHeight
        },
				set(val){this.$store.commit('common/updateDocumentClientHeight',val)}
			},
			sidebarFold: {
				get(){return this.$store.state.common.sidebarFold}
			},
      userId: {
        get () { return this.$store.state.user.id },
        set (val) { this.$store.commit('user/updateId', val) }
      },
      userName: {
        get () { return this.$store.state.user.name },
        set (val) { this.$store.commit('user/updateName', val) }
      },
      areaId:{
        get () { return this.$store.state.user.areaId },
        set (val) { this.$store.commit('user/updateAreaId', val) }
      }
		},
		mounted(){
			this.resetDocumentClientHeight()
		},
		created(){
			this.getUserInfo()
		},
		methods:{
			// 重置窗口可视高度
			resetDocumentClientHeight(){
				this.documentClientHeight=document.documentElement['clientHeight']

        window.addEventListener('resize', () => this.documentClientHeight=document.documentElement['clientHeight'], false)

				// window.onresize=()=>{
				// 	this.documentClientHeight=document.documentElement['clientHeight']
				// }
			},
			getUserInfo(){// 获取当前管理员信息
        var data=JSON.parse(sessionStorage.getItem('getUserInfo') || '[]');
        this.userId = data.userId;
        this.userName = data.userName;
        this.areaId=data.areaId;
				this.loading = false
			}
		}
	}
</script>



