<template>
  <nav class="site-navbar">
    <div class="site-navbar__header">
      <h1 class="site-navbar__brand">
        <img class="site-navbar__logo" src="../../assets/img/logo.png" />
        <a class="site-navbar__brand-lg" href="javascript:;">组工一体化平台</a>
      </h1>
    </div>
    <ul class="site-navbar_nav">
      <li v-for="(item,i) in nav" @click="navChange(item,i)" :class="i==navSel?'active':''" :key="i">
        <img :src="item.sysIcon" />
        <p>{{item.sysName}}</p>
      </li>
    </ul>
    <div class="site-navbar__body">
      <el-menu class="site-navbar__menu" mode="horizontal">
        <el-menu-item index="1" @click="updateSettingHandle">
          <icon-svg name="shezhi" class="el-icon-setting"></icon-svg>
        </el-menu-item>
        <el-menu-item index="2">
          <el-dropdown class="user-name" trigger="hover">
            <span class="el-dropdown-link userinfo-inner" style="font-family:'microsoft yahei';color: #D7E7FC;">
              <i class="iconfont icon-user"></i>
              {{name}}
              <i class="el-icon-caret-bottom"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item divided @click.native="logout">
                <i class="el-icon-remove"></i>
                退出登录
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-menu-item>
      </el-menu>
    </div>
    <!-- 弹窗,设置标签 -->
    <update-setting v-if="updateSettingVisible" ref="updateSetting"></update-setting>
  </nav>
</template>

<script>
import UpdateSetting from "./main-navbar-update-setting";
import { clearLoginInfo } from '@/utils'
export default {
  data () {
    return {
      updateSettingVisible: false,
      name: this.$store.state.user.name,
      nav: [],
      navSel: sessionStorage.getItem('navSel') || 0,
    };
  },
  components: {
    UpdateSetting
  },
  computed: {
    mainTabs: {
      get () {
        return this.$store.state.common.mainTabs;
      },
      set (val) {
        this.$store.commit("common/updateMainTabs", val);
      }
    }
  },
  created () {
    this.getUserIdByRoleInfo()
  },
  methods: {
    updateSettingHandle () {
      this.updateSettingVisible = true;
      this.$nextTick(() => {
        this.$refs.updateSetting.init();
      });
    },
    //退出登录
    logout () {
      var _this = this;
      this.$confirm("确认退出吗?", "提示", {})
        .then(() => {
          _this.$http({
            url: _this.$http.adornUrl(_this.API.loginOut),
            method: "post",
            data: _this.$http.adornData({})
          })
            .then(({ data }) => {
              if (data.code === 0) {
                clearLoginInfo()
                _this.$router.push("/login");
                window.sessionStorage.clear();
              }
            })
            .catch(function (error) {
              alert(error);
            });
        })
        .catch(() => { });
    },
    getUserIdByRoleInfo () {
      var systemId = sessionStorage.getItem('systemId')
      var data = JSON.parse(sessionStorage.getItem('navSystem') || '[]');
      this.nav = data
      // 只显示平台配置
      // let newNav  = data.filter(item => item.sysId ==systemId)
      // this.nav=newNav;
      this.$store.commit("common/updateSystemId", systemId);
    },
    navChange (item, i) {
      const loading = this.$loading({ lock: true, text: '加载中', spinner: 'el-icon-loading', background: 'rgba(0, 0, 0, 0.7)' });
      this.$store.commit("common/updateSystemId", item.sysId);
      this.navSel = i;
      sessionStorage.setItem('navSel', this.navSel)
      setTimeout(() => {
        loading.close();
      }, 500);
    }
  }
};
</script>
<style lang="scss"  scoped>
.user-name {
  margin-left: 10px;
  .el-dropdown-link {
    color: #fff;
    cursor: pointer;
  }
  .el-dropdown-menu__item {
    text-align: center;
  }
}
</style>



